<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Scientific'}">教学与科研</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Scientific',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div class="videoTitle">
          <p class="videoP">{{file.courseName}}</p>
          <div class="videoSpanDiv">
            <span class="videoSpan">
            {{file.courseTime}}
          </span>
            <span class="videoSpan">
            {{file.viewTimes}}人浏览
          </span>
          </div>
        </div>
        <div class="videoContent">
          <div id="introduce" class="introduce">
            {{setIntroduce('introduce',file.content)}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Center',       //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示文件

        videoId:'',            //视频Id
        pageSize:10,           //评论行数
        pageNum:1,             //评论页数
        videos:[],             //视频列表
        video:{},              //当前视频对象
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //显示视频介绍
      setIntroduce(id,videoIntro){
        $('#'+id).html(videoIntro)
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index,id} = this
        this.file = List[id]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },

      //消息提示
      message(message,type,duration){
        this.$message({
          message,
          type,
          duration
        })
      }
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.id = this.$route.query.id * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
        this.id = this.$route.query.id * 1
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Scientific',
          menuName:'教学成果',
          name:'ScientificDetail'
        },
      ]
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "courseName": "教学科研1",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'教学科研1',
        },
        {
          "id": "1",
          "courseName": "教学科研2",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'教学科研2',
        },
        {
          "id": "2",
          "courseName": "教学科研3",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'教学科研3',
        },
        {
          "id": "3",
          "courseName": "教学科研4",
          "courseTime": "2020-09-28",
          "viewTimes": 22,
          "content":'教学科研4',
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          min-height: 470px;
          padding-top: 10px;
          .videoTitle{
            text-align: center;
            margin: 0;
            .videoP{
              font-size: 18px;
              font-weight: bold;
              color: #333333;
              line-height: 30px;
              margin-bottom: 0;
              margin-top: 0;
            }
            .videoSpanDiv{
              line-height: 30px;
              border-bottom: 1px solid rgb(233,233,233);
              text-align: center;
              padding-bottom: 10px;
              margin-bottom: 0px;
              .videoSpan{
                display: inline-block;
                width: 100px;
                font-size: 12px;
                color: rgb(100,100,100);
              }
            }
          }
          .videoContent{
            min-height: 150px;
            .videoContentTitle{
              font-size: 18px;
              font-weight: bold;
              color: #333;
              margin-top:20px;
            }
            .introduce{
              text-indent: 2em;
              padding: 15px;
              color: #333;
              font-size: 14px;
              line-height: 23px;
              min-height: 100px;
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          .myDetail{
            .videoContent{
              min-height: 150px;
              .introduce{
                padding: 15px 0;
              }
            }
          }
        }
      }
    }
  }
</style>



